@import url('https://cdn.rawgit.com/moonspam/NanumSquare/master/nanumsquare.css');

@font-face {
  font-family: 'Yeongdo-Rg';
  src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_2202-2@1.0/Yeongdo-Rg.woff') format('woff');
  font-weight: normal;
  font-style: normal;
}

p {
  margin: 0
}
html {
  
}
.App {

  box-sizing: border-box;
  width: 100%;
  height: 100vh;
  margin: 0 auto;
  background-color: rgb(138, 138, 138);
  background-image: linear-gradient(to bottom,
      rgba(255, 255, 255, 0.1) 40%,
      rgba(255, 255, 255, 0.2) 50%,
      rgba(255, 255, 255, 0.9) 70%,
      rgba(255, 255, 255, 1) 80%),
      
    url('https://source.unsplash.com/featured/?sky');
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  
  
  text-align: center;
  font-family: nanumsquare;
  position: relative;
  
}


.styleimg img{
  width: 80%;


}


.inputWrap {
 
  display: flex;
  align-items: center;
  flex-direction: column;
  top: 450px;
  columns: center;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
}

.inputWrap p {
  margin-top: 0px;
  margin-bottom: 0px;
  font-size: 3em;
  font-weight: bold;
  color: #EC6E4C;

}

.inputWrap input {
  margin-top: 15%;
  border-top: none;
  border-left: none;
  border-right: none;
  border-bottom: 3px rgb(41, 41, 41) solid;
  background-color: transparent;
  text-align: center;
  color: #EC6E4C;
  font-size: 1rem
}

input:focus {
  outline: none;
  color: rgb(58, 56, 56)
}

input::placeholder {
  color: rgb(94, 94, 94);
  font-size: 1rem;
  opacity: 0.5;
  text-align: center;
  font-family: nanumsquare;
  margin-bottom: 10px;
  text-shadow: 5px 5px 10px rgba(0, 0, 0, 0.5);
}



.inputWrap button {
  margin-top: 10px;
  width: 75%;
  height: 40px;
  top: 56px;
  left: 0;
  right: 0;
  border: none;
  border-radius: 30px;
  background-color: #EC6E4C;
  box-shadow: 5px 5px 10px rgba(0, 0, 0, 0.2);

}

/* 날씨 */

.userName {
  display: flex;
  justify-content: center;
  margin-top: 10px;
}


.nav {
  margin-top: 60px;
  position: absolute;
  top: 20px;
  left: 0;
  right: 0;
  margin: auto;
}

.top {
  height: 50px;
  padding: 10px;
  box-sizing: border-box;
  display: flex;
  align-items: center;
  justify-content: space-between;

  font-size: 1rem;


}


.contentsWrap p {
  font-size: 1.2rem;

}

.weatherWrap {

  position: relative;
  top: 100px;
  left: 0;
  right: 0;
  margin: auto;

}


.week {
  width: 100%;
  max-width: 500px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  position: relative;
  margin: auto;
}

.week_0 {
  width:50%;
  background-color: rgba(255, 255, 255, 0.692);
  border: 1px dotted rgba(219, 219, 219, 0.542);
  border-radius: 8px;
  width: 80px;
  padding: 10px;
  display: flex;
  flex-direction: column;
  box-sizing: border-box;
  margin-bottom: 100px;
  box-shadow: 0px -5px 10px rgba(166, 166, 166, 0.172);
  font-size: 1rem;
  margin: 0 auto;
  
}

.buttonWrap {

  position: relative;
  z-index: 5;
  display: flex;
  justify-content: space-between;
  padding: 10px
}

.buttonWrap button {

  width: 55px;
  height: 40px;
  border-radius: 30px;
  border: none;
  margin-left: 10px;
  cursor: pointer;
  font-size: 1rem;
  background-color: #EC6E4C;

}


.weatherContents {
  padding: 40px;
  box-sizing: border-box;
  border-radius:  30px  30px 0 0px;
  width: 100%;
  height: 100vh;
  margin-top: 150px;
  font-size: 1.2rem;
  
}
.title2 {
  position: relative;

}
.weatherContents .box {
  width: 80%;
  height: 30px;
  background-color: #f4f4f471;
  box-shadow: 0px -5px 10px rgba(108, 108, 108, 0.172);
  
  border-radius: 10px;
  margin: 0 auto;
  margin-top: 25px;
  text-align:left;
  padding: 10px;
  
}


.toggle {
  width: 80px;
  height: 30px;
  background-color: #EC6E4C;
  border-style: none;
  border-radius: 50px;
  color: white;
  cursor: pointer;
  position: relative;
 top: 20px;
margin-bottom: 30px;
 margin-left: 00px;
 box-shadow: 0px 5px 10px rgba(108, 108, 108, 0.172);
}

